<template>
    <div>
        <h1>Hire Me</h1>
        <div class="project-container d-flex flex-row justify-content-around flex-wrap container-sm my-5">
            <div class="col-md-4 border border-light rounded" v-for="people in this.ADPeoples" :key="people.id">
                <router-link :to="{ name: 'people', params: { username: people.username }}">
                <div>{{ people.username }}</div>
                <span v-for="availability in people.availability" :key="availability.id">{{ availability.day }} | </span>
                <div>
                    <span v-if="people.onoffline.id === 3">{{ people.onoffline.title}}</span>
                    <span v-else>{{ people.onoffline.title}} Only</span>
                </div>
                <span v-for="skill in people.skill" :key="skill.title">{{ skill.title }} | </span>
                <div>
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-geo-alt" viewBox="0 0 16 16">
                    <path d="M12.166 8.94c-.524 1.062-1.234 2.12-1.96 3.07A31.493 31.493 0 0 1 8 14.58a31.481 31.481 0 0 1-2.206-2.57c-.726-.95-1.436-2.008-1.96-3.07C3.304 7.867 3 6.862 3 6a5 5 0 0 1 10 0c0 .862-.305 1.867-.834 2.94zM8 16s6-5.686 6-10A6 6 0 0 0 2 6c0 4.314 6 10 6 10z"/>
                    <path d="M8 8a2 2 0 1 1 0-4 2 2 0 0 1 0 4zm0 1a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                    </svg>
                    {{ people.location[0].city }}
                </div>
            </router-link>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            ADPeoples: [],
        }
    },
    methods: {
        getADPeoples() {
            axios({
                method: 'get',
                url: `${this.$store.state.API_URL}/accounts/adpeoples`
            })
            .then((res => {
                this.ADPeoples = res.data
            }))
            .catch(err => console.log(err))
        },
    },
    created() {
        this.getADPeoples()
    },
}
</script>

